<template>
  <div class="">
    <div class="header"></div>
    <div class="main px-12 md:px-40 ">
      <div class="RentBuy flex justify-center mt-6 float-left mb-2">
        <button
          class="border-2 px-5 p-1 text-black"
          @click="togglePurpose()"
          :class="{
            'bg-yellow-500 border-yellow-500': filters.purpose == 'for sale',
          }"
        >
          Buy
        </button>
        <button
          class="border-2 px-5 p-1 text-black"
          @click="togglePurpose()"
          :class="{
            'bg-yellow-500 border-yellow-500': filters.purpose == 'for rent',
          }"
        >
          Rental
        </button>
      </div>
      <!-- search input -->
      <div class="flex w-full border-grey-light border mb-2">
        <button>
          <span class="w-auto flex justify-end items-center text-grey p-2">
            <svg
              class="w-6 h-6 bg-yelleow-500"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="2"
                d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
              ></path>
            </svg>
          </span>
        </button>
        <input
          class="w-full rounded mr-4"
          type="text"
          placeholder="Search..."
          v-model="filters.keyword"
          @keyup="getEstates()"
        />
      </div>
      <!-- search input -->
      <!-- dropdowns -->
      <div class="flex items-center space-x-3 mb-6">
        <multiselect
          placeholder="categories"
          label="name"
          track-by="name"
          :multiple="true"
          value="CategoryID"
          v-model="filters.categories"
          :options="categories"
          :close-on-select="false"
          :show-labels="false"
          :clear-on-select="false"
        >
          <div
            class="selection-count"
            slot="selection"
            slot-scope="{ values, search, isOpen, remove }"
          >
            <template v-if="!isOpen && values.length">
              {{ values.length }} {{ values.length > 1 ? "categories" : "category" }} selected
            </template>
          </div>
          <template slot="option" slot-scope="props">
            <div class="flex justify-between items-center">
              <span>{{ props.option.name }}</span>
              <span v-if="isCategorySelected(props.option.CategoryID)" class="text-right text-sm"
                >X</span
              >
            </div>
          </template>
        </multiselect>
        <multiselect
          placeholder="countries"
          label="Name"
          track-by="Name"
          :multiple="true"
          value="CountryID"
          v-model="filters.countries"
          :options="countries"
          :close-on-select="false"
          :show-labels="false"
          :clear-on-select="false"
        >
          <div
            class="selection-count"
            slot="selection"
            slot-scope="{ values, search, isOpen, remove }"
          >
            <template v-if="!isOpen && values.length">
              {{ values.length }} {{ values.length > 1 ? "categories" : "category" }} selected
            </template>
          </div>
          <template slot="option" slot-scope="props">
            <div class="flex justify-between items-center">
              <span>{{ props.option.Name }}</span>
              <span v-if="isCountrySelected(props.option.CountryID)" class="text-right text-sm"
                >X</span
              >
            </div>
          </template>
        </multiselect>
        <input
          type="text"
          v-model="filters.minPrice"
          placeholder="Min Price"
          class="px-3 py-2 border"
        />
        <input
          type="text"
          v-model="filters.maxPrice"
          placeholder="Max Price"
          class="px-3 py-2 border"
        />
        <button @click="getEstates()" class="px-5 py-2 border-yellow-500 text-white bg-yellow-500">
          Apply
        </button>

        <button @click="displayDetails">
          <i class="far fa-plus-circle font-bold text-4xl text-yellow-500"></i>
        </button>
        <extra-filters v-if="popup" @close="popup = false" />
      </div>
      <!-- dropdowns -->

      <!-- filters -->
      <div class="flex flex-wrap space-x-3">
        <div
          v-for="category in filters.categories"
          @click="clearFilter('categories', 'CategoryID', category.CategoryID)"
          :key="category.name"
          class="bg-green-500 text-white p-3 cursor-pointer"
        >
          <span>{{ category.name }}</span> <i class="fas fa-times"></i>
        </div>
        <div
          class="bg-green-500 text-white p-3 cursor-pointer"
          @click="clearFilter('minPrice', 'minPrice', null)"
          :key="filters.minPrice"
          v-if="filters.minPrice != 0"
        >
          <span>MinPrice {{ filters.minPrice }} &euro; </span> <i class="fas fa-times"></i>
        </div>
        <div
          class="bg-green-500 text-white p-3 cursor-pointer"
          @click="clearFilter('maxPrice', 'maxPrice', null)"
          :key="filters.maxPrice"
          v-if="filters.maxPrice != 0"
        >
          <span>MaxPrice {{ filters.maxPrice }} &euro; </span> <i class="fas fa-times"></i>
        </div>
        <div
          class="bg-green-500 text-white p-3 cursor-pointer"
          v-for="country in filters.countries"
          @click="clearFilter('countries', 'CountryID', country.CountryID)"
          :key="country.Name"
        >
          <span>{{ country.Name }} </span> <i class="fas fa-times"></i>
        </div>
      </div>
      <!-- Filters -->

      <!-- Estate list -->

      <Estates :estates="estates" />
      <!-- ./EstateList -->
      <estate-pagination
        class="mt-3"
        v-if="pagination"
        :pagination="pagination"
        @paginate="paginate($event)"
        :offset="4"
      />
    </div>
  </div>
</template>

<script>
import Estates from "@/components/Estates.vue";
import EstatePagination from "@/components/EstatePagination.vue";
import Multiselect from "vue-multiselect";
import ExtraFilters from "@/components/ExtraFilters.vue";
import axios from "axios";
import _ from "lodash";
import utils from "@/helpers/utils";

export default {
  name: "properties",
  components: {
    Estates,
    EstatePagination,
    Multiselect,
    ExtraFilters,
  },
  data: function() {
    return {
      filters: {
        keyword: "",
        purpose: "for rent",
        minPrice: "",
        maxPrice: "",
        countries: [],
        categories: [],
      },
      estates: [],
      pagination: null,
      page: 1,
      countries: [],
      categories: [],
      popup: false,
    };
  },
  methods: {
    togglePurpose() {
      this.filters.purpose = this.filters.purpose == "for rent" ? "for sale" : "for rent";
      this.getEstates();
    },
    paginate(page) {
      this.page = page;
      this.getEstates();
    },
    getEstates() {
      let countries = _.map(this.filters.countries, "CountryID");
      let categories = _.map(this.filters.categories, "CategoryID");
      let countriesQueryString = utils.arrayToQueryString(countries, "countries");
      let categoriesQueryString = utils.arrayToQueryString(categories, "categories");
      let filtersQueryString = `page=${this.page}&keyword=${this.filters.keyword}&purpose=${this.filters.purpose}&minPrice=${this.filters.minPrice}&maxPrice=${this.filters.maxPrice}&${countriesQueryString}&${categoriesQueryString}`;
      console.log(filtersQueryString);
      axios
        .get(`https://apivitexport.azurewebsites.net/api/estates?${filtersQueryString}`)
        .then((response) => {
          this.estates = response.data.data;
          this.pagination = {
            current_page: response.data.current_page,
            first_page_url: response.data.first_page_url,
            from: response.data.from,
            to: response.data.to,
            last_page: response.data.last_page,
          };
          console.log(this.pagination);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getCategories() {
      axios
        .get(`https://apivitexport.azurewebsites.net/api/categories`)
        .then((response) => {
          this.categories = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getCountries() {
      axios
        .get(`https://apivitexport.azurewebsites.net/api/countries`)
        .then((response) => {
          this.countries = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    clearFilter(field, key, value) {
      switch (field) {
        case "minPrice":
          this.filters.minPrice = 0;
          break;
        case "maxPrice":
          this.filters.maxPrice = 0;
          break;
        case "categories":
          this.filters.categories = this.filters.categories.filter(
            (category) => category[key] !== value
          );
          break;
        case "countries":
          this.filters.countries = this.filters.countries.filter(
            (country) => country[key] !== value
          );
          break;
        default:
          break;
      }

      this.getEstates();
    },
    isCountrySelected: function(currentCountry) {
      return (
        this.filters.countries.filter((country) => country.CountryID === currentCountry).length > 0
      );
    },
    isCategorySelected: function(currentCategory) {
      return (
        this.filters.categories.filter((category) => category.CategoryID === currentCategory)
          .length > 0
      );
    },
    displayDetails: function() {
      this.popup = !this.popup;
    },
  },
  mounted() {
    this.getCategories();
    this.getCountries();
    this.getEstates();
  },
};
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style scoped>
*:focus {
  outline: none;
}
.bg-green-500 {
  background: #f59e0b;
  font-size: 12px;
  width: auto;
  padding: 5px;
  border-radius: 10px;
}
</style>
